.rocket {
    position: absolute;
    width: 80px;
    left: calc(50% - 60px);
}
.rocket .rocket-body {
    width: 80px;
    left: calc(50% - 50px);
    -webkit-animation: bounce 1.5s infinite;
            animation: bounce 1.5s infinite;
}
.rocket .rocket-body .body {
    background-color: #dadada;
    height: 180px;
    left: calc(50% - 50px);
    border-top-right-radius: 100%;
    border-top-left-radius: 100%;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    border-top: 5px solid #f5f5f5;
}
.rocket .rocket-body:before {
    content: '';
    position: absolute;
    left: calc(50% - 24px);
    width: 48px;
    height: 13px;
    background-color: #554842;
    bottom: -13px;
    border-bottom-right-radius: 60%;
    border-bottom-left-radius: 60%;
}
.rocket .window {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: #a75248;
    left: calc(50% - 25px);
    top: 40px;
    border: 5px solid #b4b2b2;
}
.rocket .fin {
    position: absolute;
    z-index: -100;
    height: 55px;
    width: 50px;
    background-color: #a75248;
}
.rocket .fin-left {
    left: -30px;
    top: calc(100% - 55px);
    border-top-left-radius: 80%;
    border-bottom-left-radius: 20%;
}
.rocket .fin-right {
    right: -30px;
    top: calc(100% - 55px);
    border-top-right-radius: 80%;
    border-bottom-right-radius: 20%;
}
.rocket .exhaust-flame {
    position: absolute;
    top: 90%;
    width: 28px;
    background: linear-gradient(to bottom, transparent 10%, #f5f5f5 100%);
    height: 150px;
    left: calc(50% - 14px);
    -webkit-animation: exhaust 0.2s infinite;
            animation: exhaust 0.2s infinite;
}
.rocket .exhaust-fumes li {
    width: 60px;
    height: 60px;
    background-color: #f5f5f5;
    list-style: none;
    position: absolute;
    border-radius: 100%;
}
.rocket .exhaust-fumes li:first-child {
    width: 200px;
    height: 200px;
    bottom: -300px;
    -webkit-animation: fumes 5s infinite;
            animation: fumes 5s infinite;
}
.rocket .exhaust-fumes li:nth-child(2) {
    width: 150px;
    height: 150px;
    left: -120px;
    top: 260px;
    -webkit-animation: fumes 3.2s infinite;
            animation: fumes 3.2s infinite;
}
.rocket .exhaust-fumes li:nth-child(3) {
    width: 120px;
    height: 120px;
    left: -40px;
    top: 330px;
    -webkit-animation: fumes 3s 1s infinite;
            animation: fumes 3s 1s infinite;
}
.rocket .exhaust-fumes li:nth-child(4) {
    width: 100px;
    height: 100px;
    left: -170px;
    -webkit-animation: fumes 4s 2s infinite;
            animation: fumes 4s 2s infinite;
    top: 380px;
}
.rocket .exhaust-fumes li:nth-child(5) {
    width: 130px;
    height: 130px;
    left: -120px;
    top: 350px;
    -webkit-animation: fumes 5s infinite;
            animation: fumes 5s infinite;
}
.rocket .exhaust-fumes li:nth-child(6) {
    width: 200px;
    height: 200px;
    left: -60px;
    top: 280px;
    -webkit-animation: fumes2 10s infinite;
            animation: fumes2 10s infinite;
}
.rocket .exhaust-fumes li:nth-child(7) {
    width: 100px;
    height: 100px;
    left: -100px;
    top: 320px;
}
.rocket .exhaust-fumes li:nth-child(8) {
    width: 110px;
    height: 110px;
    left: 70px;
    top: 340px;
}
.rocket .exhaust-fumes li:nth-child(9) {
    width: 90px;
    height: 90px;
    left: 200px;
    top: 380px;
    -webkit-animation: fumes 20s infinite;
            animation: fumes 20s infinite;
}
@-webkit-keyframes fumes {
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        background-color: transparent;
   }
    51% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
   }
    100% {
        background-color: #f5f5f5;
        -webkit-transform: scale(1);
                transform: scale(1);
   }
}
@keyframes fumes {
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        background-color: transparent;
   }
    51% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
   }
    100% {
        background-color: #f5f5f5;
        -webkit-transform: scale(1);
                transform: scale(1);
   }
}
@-webkit-keyframes bounce {
    0% {
        -webkit-transform: translate3d(0px, 0px, 0);
                transform: translate3d(0px, 0px, 0);
   }
    50% {
        -webkit-transform: translate3d(0px, -5vh, 0);
                transform: translate3d(0px, -5vh, 0);
   }
    100% {
        -webkit-transform: translate3d(0px, 0px, 0);
                transform: translate3d(0px, 0px, 0);
   }
}
@keyframes bounce {
    0% {
        -webkit-transform: translate3d(0px, 0px, 0);
                transform: translate3d(0px, 0px, 0);
   }
    50% {
        -webkit-transform: translate3d(0px, -5vh, 0);
                transform: translate3d(0px, -5vh, 0);
   }
    100% {
        -webkit-transform: translate3d(0px, 0px, 0);
                transform: translate3d(0px, 0px, 0);
   }
}
@-webkit-keyframes exhaust {
    0% {
        background: linear-gradient(to bottom, transparent 10%, #f5f5f5 100%);
   }
    50% {
        background: linear-gradient(to bottom, transparent 8%, #f5f5f5 100%);
   }
    75% {
        background: linear-gradient(to bottom, transparent 12%, #f5f5f5 100%);
   }
}
@keyframes exhaust {
    0% {
        background: linear-gradient(to bottom, transparent 10%, #f5f5f5 100%);
   }
    50% {
        background: linear-gradient(to bottom, transparent 8%, #f5f5f5 100%);
   }
    75% {
        background: linear-gradient(to bottom, transparent 12%, #f5f5f5 100%);
   }
}
@-webkit-keyframes fumes2 {
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
   }
}
@keyframes fumes2 {
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
   }
}
@-webkit-keyframes twinkle {
